<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
};
</script>

<style>

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 1440px;
  height: 100vh;
  margin: 0 auto;
}

.flex-container {
  display: flex;
  width: 100%;
  height: 100%;
}

.flex-center-align {
  justify-content: center;
  align-content: center;
  align-items: center;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.fade-out {
  animation: fadeOut 0.5s ease-in-out both;
}

.fade-in {
  animation: fadeIn 0.5s ease both;
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-50vh);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(50vh);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>



