  </div>
</template>

<script>
export default {
  name: 'TextLine',
};
</script>

<style scoped>
.text {
  position: absolute;
  transform: translate(320px, -300px);
}

p {
  margin: 0;
  padding: 0;
  height: 0;
  width: 800px;

  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -1px;
  text-align: right;
  color: #888888;
}
</style>



