<template>
  <header>
    <ul class="wrapper-ul">
      <li class="wrapper-li">
        <div class="left">
          <p>INDEX</p>
          <div class="line1"></div>
        </div>
      </li>
      <li class="wrapper-li">
        <div class="progress flex-container flex-center-align" v-show="this.progressOn">
          <div class="progress-line"></div>
          <ul class="progress-ul">
            <li class="progress-li" v-for="(count, index) in progressCountSet" :key="count">
              <div :class="{'circle-active': index === scrollPos, 'circle': index !== scrollPos}">
              </div>
            </li>
          </ul>
        </div>
      </li>
      <li class="wrapper-li">
        <div class="right">
          <p>INFO</p>
          <div class="line2"></div>
        </div>
      </li>
    </ul>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      progressCountSet: [0, 1, 2, 3, 4, 5, 6, 7],
      scrollPos: 0,
    };
  },
  props: {
    progressOn: Boolean,
  },
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.scrollPos = Math.floor((window.scrollY) / 660);
    },
  },
};
</script>

<style scoped>

.progress {

}

.progress-ul {
  margin-top: 20px;
  position: absolute;
  display: flex;
  width: 400px;
  height: 50px;
  list-style-type: none;
}

.progress-li {
  flex-grow: 1;
  display: inline-block;
}

.progress-line {
  width: 360px;
  height: 1px;
  background-color: #bbbbbb;
}

.circle-active {
  width: 10px;
  height: 10px;
  background-color: #888888;
  border-radius: 10px;
}

.circle {
  width: 10px;
  height: 10px;
  border: solid 1px #eeeeee;
  border-radius: 10px;
}

.line1 {
  position: absolute;
  width: 80px;
  height: 1px;
  background-color: #bbbbbb;
  bottom: 40px;
  left: 20px;
}

.line2 {
  position: absolute;
  width: 80px;
  height: 1px;
  background-color: #bbbbbb;
  bottom: 40px;
  right: -46px;
}

header {
  width: 1440px;
  height: 120px;
  position: fixed;
  z-index: 10;
}

.wrapper-ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 1440px;
  height: 120px;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.wrapper-li {
  flex-grow: 1;
}

.left {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  align-content: center;
  text-align: left;
}

.right {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: flex-end;
  text-align: right;
}

p {
  font-size: 18px;
  font-weight: 300;
  letter-spacing: -0.3px;
  color: #444444;
}
</style>



