<template>
  <div class="sidebar-wrapper">
    <div class="sidebar">
      <ul>
        <li v-for="(name, index) in projectNameArray" :key="index">
          <p :class="{'name-active': index === scrollPos,
                      'name': index !== scrollPos}">{{name}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProjectSideBar',
  data() {
    return {
      scrollPos: 0,
    };
  },
  props: {
    projectNameArray: Array,
  },
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      this.scrollPos = Math.floor((window.scrollY) / 660);
    },
  },
};
</script>

<style scoped>
ul {
  list-style-type: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

li {
  margin: 0;
  width: 100%;
  height: 100%;
}

p {
  margin-top: 10px;
  margin-bottom: 10px;
}

.sidebar-wrapper {
  margin-right: 120px;
  position: fixed;
  right: 0;
  bottom: 50%;
  transform: translate(0, 50%);
}

.sidebar {
  position: absolute;
  right: 0;
  bottom: 50%;
  transform: translate(0, 50%);
}

.name {
  width: 300px;
  height: 100%;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: -0.2px;
  text-align: right;
  color: #eeeeee;
}

.name-active {
  width: 300px;
  height: 100%;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: -0.2px;
  text-align: right;
  color: #888888;
}
</style>



