<template>
  <div class="wrapper">
    <Header :progressOn="false"></Header>
    <div class="info">
      <div class="flex-container flex-center-align">
        <title-box></title-box>
      </div>
      <text-line></text-line>
    </div>
  </div>
</template>

<script>
import TitleBox from '@/components/TitleBox';
import Header from '@/components/Header';
import TextLine from '@/components/TextLine';

export default {
  name: 'MainPage',
  data() {
    return {
      nextPage: false,
    };
  },
  components: {
    TitleBox,
    Header,
    TextLine,
  },
  methods: {
    handleScroll() {
      if ((window.innerHeight + window.scrollY) >= document.body.offsetHeight + 10) {
        if (this.nextPage === false) {
          setTimeout(() => {
            const wrapper = document.querySelector('.info');
            wrapper.classList.remove('fade-out');
            location.href = '/#/projects';
          }, 600);

          const wrapper = document.querySelector('.info');
          wrapper.classList.add('fade-out');
          this.nextPage = true;
        }
      }
    },
  },
  beforeMount() {
    window.addEventListener('scroll', this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll);
  },
};
</script>

<style scoped>
.info {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  height: 100%;
}
</style>



