<template>
  <div class="wrapper">
    <div class="number-wrapper">
      <span class="number">{{this.count}}</span>
    </div>
    <div class="project-wrapper">
      <span class="project-name">{{this.name}}</span>
      <span class="project-subtitle">{{this.part}}</span>
    </div>
    <div class="img-wrapper">
      <div class="img" v-bind:style="{backgroundImage: this.imgTest}"></div>
    </div>
  </div>
</template>

<script>
import test from '@/assets/test.jpg';

export default {
  name: 'Project',
  data() {
    return {
      imgTest: 'url(' + (this.img || test) + ')',
      count: this.title < 10 ? '0' + this.title.toString() : this.title.toString(),
    };
  },
  props: {
    title: Number,
    name: String,
    part: String,
    img: String,
  },
};
</script>

<style scoped>
.wrapper {
  width: 600px;
  height: 600px;
  position: relative;
}

.number-wrapper {
  display: inline-block;
  position: relative;
  width: 180px;
  height: 70px;
  margin-left: -80px;
}

.number {
  position: absolute;
  left: 0;
  top: 0;
  font-size: 144px;
  font-weight: 900;
  letter-spacing: -6px;
  text-align: right;
  color: #f5f5f5;
  z-index: -1;
}

.project-wrapper {
  display: inline-block;
}

.project-name {
  display: block;
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -1px;
  text-align: left;
  color: #444444;
}

.project-subtitle {
  display: block;
  font-size: 18px;
  font-weight: 300;
  letter-spacing: -0.7px;
  text-align: left;
  color: #bbbbbb;
}

.img-wrapper {
  margin-top: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  width: 600px;
  height: 400px;
}

.img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  box-shadow: 0 10px 6px -6px #777;
}
</style>



