<template>
  <div class="wrapper">
    <Header :progressOn="true"></Header>
    <div class="info fade-in">
      <div class="list flex-container">
        <ul>
          <li v-for="(data, index) in this.datas" v-bind:key="index">
            <project :title="index + 1"
                     :name="data.name"
                     :part="data.part"
                     :img="data.img"></project>
          </li>
        </ul>
      </div>
    </div>
    <ProjectSideBar :projectNameArray="datas.map((data)=>{return data.name})"></ProjectSideBar>
  </div>
</template>

<script>
import Header from '@/components/Header';
import Project from '@/components/Project';
import ProjectSideBar from '@/components/ProjectSideBar';

export default {
  name: 'ProjectPage',
  components: {
    Header,
    Project,
    ProjectSideBar,
  },
  data() {
    return {
      datas: [
        {
          name: 'Afitcionado',
          part: 'UX/UI DESIGN',
          img: null,
        },
        {
          name: 'LikeStar',
          part: 'UX/UI DESIGN',
          img: null,
        },
        {
          name: '우리동네 스카이',
          part: 'UX/UI DESIGN',
          img: null,
        },
        {
          name: '지구조각가',
          part: 'UX/UI DESIGN',
          img: null,
        },
        {
          name: 'ROOMY',
          part: 'UX/UI DESIGN',
          img: null,
        },
        {
          name: 'Redot',
          part: 'BRAND + UX&UI Design',
          img: null,
        },
        {
          name: 'Sooya Pet',
          part: 'LOGO DESIGN',
          img: null,
        },
        {
          name: '조랭이떡 십이지신 일러스트',
          part: 'ILLUSTRATION',
          img: null,
        },
      ],
    };
  },
};
</script>

<style scoped>
.wrapper {
  width: 100%;
  height: 100%;
  padding-bottom: 15vh;
}

.info {
  width: 100%;
  height: 100%;
}

ul {
  list-style-type: none;
}

.list {
  padding-top: 25vh;
  justify-content: center;
  align-content: center;
}
</style>



