import Vue from 'vue';
import Router from 'vue-router';
import MainPage from '@/pages/MainPage';
import ProjectPage from '@/pages/ProjectPage';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MainPage',
      component: MainPage,
    },
    {
      path: '/projects',
      name: 'Projects',
      component: ProjectPage,
    },
  ],
});



